import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import { EMAIL } from '../constants'

import './sass/Disclaimer.scss'

import { Modal } from 'antd'

class Disclaimer extends Component {
  constructor (props) {
    super(props)
    this.state = {
      visible: false
    }
  }

  // a function to return the  text
  flatbread () {
    return (
      <>
        <p>
                    
        </p>
        <p>
                 
        </p>
        <p>
               ,         
        </p>
        <p>
            (     ,   )       
        </p>
        <p>
                    ,        
        </p>
        <p>
                    
        </p>
        <p>
                         , <a href={`mailto: ${EMAIL}`}> </a>:
        </p>
        <p>
                       
        </p>

        <p> 
          <Link to='/art-credits' onClick={() => this.setState({ visible: false })}>
            
          </Link>:
        </p>
      </>
    )
  }

  render () {
    return (
      <div className='disclaimer'>
        <Modal
          centered
          visible={this.state.visible}
          onCancel={() => this.setState({ visible: false })}
          maskClosable keyboard
          footer={null}
        >
          {this.flatbread()}
        </Modal>
                  
        <span onClick={() => this.setState({ visible: true })} className='clickable highlight'>  </span>
      </div>
    )
  }
}

export default Disclaimer
