import React, { Component } from 'react'

import { BASE_URL, EMAIL } from '../constants'

import './sass/SocialNetworks.scss'

import { Button, Modal } from 'antd'

class Write extends Component {
  constructor (props) {
    super(props)
    this.state = {
      visible: false
    }
  }

  buttons () {
    const self = `${BASE_URL}/assets/master/social-networks`
    return (
      <div className='buttons'>
        <Button href={`mailto: ${EMAIL}`} target='_blank' rel='noreferrer'>
          <img src={`${self}/mail-logo.png`} alt='mail' title='  ' />
        </Button>
        <Button href='https://www.facebook.com/hy.hyotaku' target='_blank' rel='noreferrer'>
          <img src={`${self}/fb-logo.png`} alt='fb' title=' fb.com - ' />
        </Button>
        <Button href='https://discord.gg/cMbHtDCTdU' target='_blank' rel='noreferrer'>
          <img src={`${self}/discord-logo.png`} alt='discord' title=' discord ' />
        </Button>
      </div>
    )
  }

  render () {
    const { popup } = this.props

    if (!popup) {
      return <div className='social-networks'> {this.buttons()} </div>
    }

    return (
      <>
        <Modal
          className='social-networks'
          centered
          maskClosable keyboard
          visible={this.state.visible}
          onCancel={() => this.setState({ visible: false })}
          footer={null}
        >
          {this.buttons()}
        </Modal>
        <a onClick={() => this.setState({ visible: true })}> </a>
      </>
    )
  }
}

export default Write
